.metadata-display {
    margin: 20px;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
  }
  
  th, td {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  th {
    background-color: #f2f2f2;
  }
  
  h3 {
    margin-top: 20px;
  }
  
  .loading {
    font-style: italic;
    color: #666;
  }
  